<template>
          <form ref="form" @submit.prevent="createAccount">
            <v-container class="py-0">
              <v-row>
                <v-col
                  cols="12"
                  md="6"
                >
                  <v-textarea
                    name="que_chi"
                    counter
                    :label="$vuetify.lang.t('$vuetify.que_chi')"
                    auto-grow
                    v-model="que_chi"
                  ></v-textarea>
                </v-col>
                <v-col
                  cols="12"
                  md="6"
                >
                  <v-textarea
                    name="que_eng"
                    counter
                    :label="$vuetify.lang.t('$vuetify.que_eng')"
                    auto-grow
                    v-model="que_eng"
                  ></v-textarea>
                </v-col>
                <v-col
                  cols="12"
                >
                  <v-file-input
                    accept="image/*,audio/*,video/*"
                    :label="$vuetify.lang.t('$vuetify.file_input')"
                  ></v-file-input>
                </v-col>
                <v-col
                  cols="12"
                >
                <v-autocomplete
                  :items="que_type_lists"
                  :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
                  item-value="id"
                  color="blue"
                  item-color="blue"
                  :label="$vuetify.lang.t('$vuetify.que_type')"
                  v-model="que_type_id"
                  clearable
                  @change="(event) => updateQueType(event)"
                ></v-autocomplete>
                </v-col> 
                <v-col
                  cols="12"
                >
                  <keep-alive>
                    <component :is="userComponent"/>
                  </keep-alive>
                </v-col>                
              </v-row>
            </v-container>
          </form>
</template>

<script>
import axios from "axios";
import VImageInput from 'vuetify-image-input';
import MultipleChoiceForm from '@/components/MultipleChoiceForm';
import FillInTheBlanksForm from '@/components/FillInTheBlanksForm';
import LongQuestionForm from '@/components/LongQuestionForm';

export default {
  data: () => {
    return {
        que_chi: "",
        que_eng: "",
        username: "",
        password: "",
        acc_name_chi: "",
        acc_name_eng: "",
        school_type_id: "",
        school_id: "",
        user_type_id: "",
        que_type_id: "",
        que_type_lists: [],
        show_pw: false, 
    }
     
  },
  name: 'QuestionForm',
  components: {
    VImageInput, MultipleChoiceForm, FillInTheBlanksForm, LongQuestionForm
  },
  computed: {
    userComponent() {
      if (this.que_type_id == "") return null;
      if (this.que_type_id == "1") return MultipleChoiceForm;
      if (this.que_type_id == "2") return FillInTheBlanksForm;
      if (this.que_type_id == "3") return LongQuestionForm;
    },
  },
  mounted() {
    this.loadQuestionType();
  },       
  methods: {
      updateQueType(value) {
        if(value == null) {
          this.que_type_id = "";
        } else {
          this.que_type_id = value;
        }
      },

      async loadSchool(school_type_id = null, school_select = false) {
        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.full_school_lists = this.full_school_lists.length != 0 ? this.full_school_lists : [];
        this.school_lists = [];
        if(school_type_id == null) {
          
          
          //let lang = this.$vuetify.lang.current;
          axios.get(
            "http://192.168.12.159:8003/api.php/school"+(school_type_id == null ? '' : ('/'+school_type_id)),
            { 
              headers: { 'Content-Type': 'application/json' } 
            }
          ).then(function(response){
            if(!Array.isArray(response.data)) {
              response.data = [response.data];
            }
            self.full_school_lists = response.data.map((items)=>{
              return {
                  id: items.school_id,
                  name_chi: items.school_chi,
                  name_eng: items.school_eng,
                  school_type: items.school_type,
                  ...items.fields
              }
            });
            self.school_lists = self.full_school_lists;
          }).catch(function(error){
            console.log(error)
          });
        } else {
            if(school_select == false) {
              self.school_id = "";
            }
            self.school_lists = self.full_school_lists.filter(f => f.school_type.includes(school_type_id));
        }
      },
      async loadQuestionType() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.que_type_lists = [];
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/question_type",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.que_type_lists = response.data.map((items)=>{
            return {
                id: items.qt_id,
                name_chi: items.qt_chi,
                name_eng: items.qt_eng,                
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },

      async loadSchoolType() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.school_type_lists = [];
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/school_type",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.school_type_lists = response.data.map((items)=>{
            return {
                id: items.school_type_id,
                name_chi: items.school_type_chi,
                name_eng: items.school_type_eng,                
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },

      async loadUserType() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.user_type_lists = [];
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/user_type",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.user_type_lists = response.data.map((items)=>{
            return {
                id: items.user_type_id,
                name_chi: items.user_type_chi,
                name_eng: items.user_type_eng,                 
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },  
      
      async loadUserInfo() {
        
        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.user_info = [];
        var acc_id = this.$route.params.id ? this.$route.params.id : null;
        if(acc_id != null) {
          axios.get(
            "http://192.168.12.159:8003/api.php/user/"+acc_id,
            { 
              headers: { 'Content-Type': 'application/json' } 
            }
          ).then(function(response){
            self.user_info        = response.data;
            self.username       = self.user_info.login_name;
            self.password         = self.user_info.password;
            self.acc_name_chi     = self.user_info.acc_name_chi;
            self.acc_name_eng     = self.user_info.acc_name_eng;
            self.school_type_id   = self.user_info.school_type_id;
            self.school_id        = self.user_info.school_id;
            self.user_type_id     = self.user_info.user_type_id;
            if(self.school_type_id != null) {
              self.loadSchool(self.school_type_id, true);
            }
          }).catch(function(error){
            console.log(error)
          });
        }
      },

    async createAccount(e) {
          e.preventDefault();
          const response = await fetch("https://ncn11iius9.execute-api.us-east-1.amazonaws.com/default/function-create-account", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              username: this.username,
              password: this.password,
            }),
          });
          const { token } = await response.json();
          console.log(token);

        },           
  },
}
</script>
