                <template>
                  <v-row> 
                  <v-col col="12" class="col-12">
<template>
  <v-data-table
              :headers="headers"
              :items="tasks.items"
  >
      <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title v-for="(title, t) in tasks.titles" :key="t">
            {{ $vuetify.lang.t(title['text']) }}
        </v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <template v-slot:activator="{ on }">
                      <span v-for="(button, b) in tasks.buttons" :key="b">
                              <app-btn
                              color="blue"
                              class="white--text"
                              :to="{ path: button['link'], params: button['params'] }"

          rel="noopener noreferrer"
        >
<v-icon>
        {{ button['icon'] }}
      </v-icon>
          {{ $vuetify.lang.t(button['text']) }}
        </app-btn>
                    </span>
          </template>   
     </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>     
        </v-toolbar>
          </template>
    <template v-slot:item.id="{ item }">
      <v-btn 
        class="mr-2"
        color="blue"
        icon 
        small
        @click="editItem(item)"
      >
        <v-icon color="blue">mdi-pencil</v-icon>
      </v-btn>

      <v-btn 
        color="red"
        icon 
        small
        @click="deleteItem(item)"
      >
        <v-icon color="red">mdi-delete</v-icon>
      </v-btn>
    </template>          
        </v-data-table>
  </template>
                    </v-col>
                    </v-row>
                    </template>
<script>
export default {
    name: 'AccountRightList',

    data: () => ({
      dialog: false,
      dialogDelete: false,
    tasks: {
      titles : [
        {
          text: '$vuetify.account_right_list',
        },        
      ],
      buttons:[
        {
          text: '$vuetify.create_account_right',
          link: '/Admin/AccountRight/Create',
          icon: 'mdi-key-plus',
          params: {page:'AccountRight', action:'Create'},
        },        
      ],
      headers: [        
        {
          sortable: false,
          text: '#',
          value: 'number',
        },
        {
          sortable: true,
          text: 'Login Name',
          value: 'login',
          align: 'left',
        },        
        {
          sortable: true,
          text: 'User Name(Chinese)',
          value: 'name_chi',
          align: 'left',
        },
        {
          sortable: true,
          text: 'User Name(English)',
          value: 'name_eng',
          align: 'left',
        },
        {
          sortable: true,
          text: 'User Type',
          value: 'user_type',
          align: 'left',
        },  
        {
          sortable: true,
          text: 'School',
          value: 'school',
          align: 'left',
        }, 
        {
          sortable: false,
          text: 'Action',
          value: 'id',
        },
      ],
      items: [
        {
          number: 1,
          id: 1,
          user_type_chi: '超級管理員',
          user_type_eng: 'Superadmin',
        },
        {
          number: 2,
          id: 2,
          user_type_chi: '校長',
          user_type_eng: 'Principal',
        },
        {
          number: 3,
          id: 3,
          user_type_chi: '老師',
          user_type_eng: 'Teacher',
        },
        {
          number: 4,
          id: 4,
          user_type_chi: '學生',
          user_type_eng: 'Student',
        },
      ]
    }
}),
    methods: {
      editItem (item) {
        this.$router.push({ path: `/Admin/AccountRight/Edit/${item.id}` });
      },
      deleteItem (item) {
        this.dialogDelete = true
      },  
      closeDelete () {
        this.dialogDelete = false
      },
      deleteItemConfirm () {
        this.closeDelete()
      },                
    },
computed: {   
    headers () {
      return [        
        {
          sortable: false,
          text: '#',
          value: 'number',
        },
        {
          sortable: true,
          text: this.$vuetify.lang.t('$vuetify.user_type_chi'),
          value: 'user_type_chi',
          align: 'left',
        },
        {
          sortable: true,
          text: this.$vuetify.lang.t('$vuetify.user_type_eng'),
          value: 'user_type_eng',
          align: 'left',
        },
        {
          sortable: false,
          text: this.$vuetify.lang.t('$vuetify.action'),
          value: 'id',
        },
      ]}
}    

}
</script>
