<template>
  <v-row> 
    <v-col col="12" class="col-12">
      <template>
        <v-data-table
          :headers="headers"
          :items="items"
          :search="search"
          :custom-filter="filterOnlyCapsText"
          :loading="loadTable"
          loading-text="Loading... Please wait"
          multi-sort
        >
        
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title v-for="(title, t) in titles" :key="t">
                {{ $vuetify.lang.t(title['text']) }}
              </v-toolbar-title>
              <v-divider
                class="mx-4"
                inset
                vertical
              ></v-divider>
              <v-spacer></v-spacer>
              <!--<v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                :label="$vuetify.lang.t('$vuetify.search')"
                single-line
                hide-details
                class="pa-3"
                width="200px"
              ></v-text-field>-->
              <v-dialog
                v-model="dialog"
                max-width="500px"
              >
                <template v-slot:activator="{ on }">
                  <span v-for="(button, b) in buttons" :key="b">
                    <app-btn
                      color="blue"
                      class="white--text"
                      :to="{ path: button['link'], params: button['params'] }"
                      rel="noopener noreferrer"
                    >
                      <v-icon>
                        {{ button['icon'] }}
                      </v-icon>
                      {{ $vuetify.lang.t(button['text']) }}
                    </app-btn>
                  </span>
                </template>   
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                      <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
              </v-dialog>     
            </v-toolbar>
          </template>
          <template v-slot:item.user_type="{ item }">
            {{ $vuetify.lang.current === 'en' ? item.user_type_eng : item.user_type_chi  }}
          </template>
          <template v-slot:item.school="{ item }">
            {{ $vuetify.lang.current === 'en' ? item.school_eng : item.school_chi  }}
          </template>          
          <template v-slot:item.id="{ item }">
            <v-btn 
              class="mr-2"
              color="blue"
              icon 
              small
              :to="{ name: 'AccountEdit', params: { id: item.id, login_id: item.login }}"
            >
              <v-icon color="blue">mdi-pencil</v-icon>
            </v-btn>

            <v-btn 
              color="red"
              icon 
              small
              @click="deleteItem(item)"
            >
              <v-icon color="red">mdi-delete</v-icon>
            </v-btn>
          </template>    
      <template v-slot:body.prepend>
        <tr :class="{'v-data-table__mobile-table-row':isMobile}">
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}"><v-icon color="gray">mdi-magnify</v-icon></td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-text-field
              v-model="loginSearch"
              :label="$vuetify.lang.t('$vuetify.login_name')"
            ></v-text-field>
          </td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-text-field
              v-model="namechiSearch"
              :label="$vuetify.lang.t('$vuetify.name_chi')"
            ></v-text-field>            
          </td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-text-field
              v-model="nameengSearch"
              :label="$vuetify.lang.t('$vuetify.name_eng')"
            ></v-text-field>
          </td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-autocomplete
              :items="user_type_lists"
              :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
              item-value="id"              
              color="blue"
              item-color="blue"
              :label="$vuetify.lang.t('$vuetify.user_type')"
              v-model="user_type_id"
              clearable
              @change="(event) => usertypeS(event)"
            ></v-autocomplete>            
          </td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-autocomplete
              :items="school_lists"
              :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
              item-value="id"                 
              color="blue"
              item-color="blue"
              :label="$vuetify.lang.t('$vuetify.school')"
              v-model="school_id"
              clearable
              @change="(event) => schoolS(event)"
            ></v-autocomplete>            
          </td>
          <td></td>
        </tr>
      </template>                         
        </v-data-table>
      </template>
    </v-col>
  </v-row>
</template>
<script>
import axios from "axios";
export default {
    name: 'AccountList',

    data: () => ({
      search: '',
      loginSearch: '',
      nameengSearch: '',
      namechiSearch: '',
      user_type_lists: [],  
      school_lists: [],
      user_type_id: "",  
      usertypeSearch: "",
      schoolSearch: "",
      school_id: "",  
      loadTable: true,
      dialog: false,
      dialogDelete: false,
      titles : [
        {
          text: '$vuetify.account_list',
        },        
      ],
      buttons:[
        {
          text: '$vuetify.create_account',
          link: '/Admin/Account/Create',
          icon: 'mdi-account-plus',
          params: {page:'Account', action:'Create'},
        },        
      ],
      
      items: [/*
        {
          number: 1,
          id: 1,
          login: 'tm.chan',
          name_chi: '陳大文',
          name_eng: 'Chan Tai Man',
          user_type: 'Teacher',
          user_type_chi: '老師',
          user_type_eng: 'Teacher',
          school_chi: '現代小學',
          school_eng: 'Modern Kindergarten',
        },
        {
          number: 2,
          id: 2,
          login: 'sf.wong',
          name_chi: '王小虎',
          name_eng: 'Wong Siu Fu',
          user_type: 'Principal',
          user_type_chi: '校長',
          user_type_eng: 'Principal',          
          school_chi: '現代小學',
          school_eng: 'Modern Kindergarten',
        },
        {
          number: 3,
          id: 3,
          login: 'sm.lee',
          name_chi: '李小明',
          name_eng: 'Lee Siu Ming',
          user_type: 'Teacher',
          user_type_chi: '老師',
          user_type_eng: 'Teacher',          
          school_chi: '現代小學',
          school_eng: 'Modern Kindergarten',
        },
        {
          number: 4,
          id: 4,
          login: 's.ho',
          name_chi: '何笑',
          name_eng: 'Ho Siu',
          user_type: 'Principal',
          user_type_chi: '校長',
          user_type_eng: 'Principal',          
          school_chi: '現代小學',
          school_eng: 'Modern Kindergarten',
        },
        {
          number: 5,
          id: 5,
          login: 'tn.cheung',
          name_chi: '張大力',
          name_eng: 'Cheung Tai Nic',
          user_type: 'Teacher',
          user_type_chi: '老師',
          user_type_eng: 'Teacher',          
          school_chi: '現代小學',
          school_eng: 'Modern Kindergarten',
        },
      */]
      
    }),
    
    mounted() {
      this.loadItems(); 
      this.loadUserType();
      this.loadSchool();
    },


    methods: {
      deleteItem (item) {
        this.dialogDelete = true
      },  
      closeDelete () {
        this.dialogDelete = false
      },
      deleteItemConfirm () {
        this.closeDelete()
      }, 

      async loadItems() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.items = [];
        let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/user",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){

          let list_number = 1;
          self.items = response.data.map((items)=>{
            return {
                number: list_number++,
                id: items.acc_id,
                login: items.login_name,
                name_chi: items.acc_name_chi,
                name_eng: items.acc_name_eng,
                user_type: items.user_type_id,
                user_type_chi: items.user_type_chi,
                user_type_eng: items.user_type_eng,
                school: items.school_id,
                school_chi: items.school_chi,
                school_eng: items.school_eng,
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
        this.loadTable= false;
      },
      filterOnlyCapsText (value, search, item) {
        return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
      },
      async loadUserType() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.user_type_lists = [];
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/user_type",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.user_type_lists = response.data.map((items)=>{
            return {
                id: items.user_type_id,
                name_chi: items.user_type_chi,
                name_eng: items.user_type_eng,                 
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },       
      async loadSchool() {
        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.school_lists = [];
          //let lang = this.$vuetify.lang.current;
          axios.get(
            "http://192.168.12.159:8003/api.php/school",
            { 
              headers: { 'Content-Type': 'application/json' } 
            }
          ).then(function(response){
            self.school_lists = response.data.map((items)=>{
              return {
                  id: items.school_id,
                  name_chi: items.school_chi,
                  name_eng: items.school_eng,
                  school_type: items.school_type,
                  ...items.fields
              }
            });
          }).catch(function(error){
            console.log(error)
          });
      },
      usertypeS(value) {
        this.usertypeSearch = value == null ? '' : value
      },
      schoolS(value) {
        this.schoolSearch = value == null ? '' : value
      }               
    },
    computed: {   
        headers () {
          return [
            {
              sortable: false,
              text: '#',
              value: 'number',
            },
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.login_name'),
              value: 'login',
              align: 'left',
              filter: value => {
                if (!this.loginSearch) return true
                return value.toLowerCase().includes(this.loginSearch.toLowerCase())
              },              
            },        
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.name_chi'),
              value: 'name_chi',
              align: 'left',
              filter: value => {
                if (!this.namechiSearch) return true
                return value.toLowerCase().includes(this.namechiSearch.toLowerCase())
              },                
            },
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.name_eng'),
              value: 'name_eng',
              align: 'left',
              filter: value => {
                if (!this.nameengSearch) return true
                return value.toLowerCase().includes(this.nameengSearch.toLowerCase())
              },                 
            },
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.user_type'),
              value: 'user_type',
              align: 'left',
              filter: value => {
                if (!this.usertypeSearch) return true
                return value.toLowerCase().includes(this.usertypeSearch.toLowerCase())
              },                
            },  
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.school'),
              value: 'school',
              align: 'left',
              filter: value => {
                if (!this.schoolSearch) return true
                return value.toLowerCase().includes(this.schoolSearch.toLowerCase())
              },                    
            }, 
            {
              sortable: false,
              text: this.$vuetify.lang.t('$vuetify.action'),
              value: 'id',
            },
          ]},
  isMobile(){
    return this.$vuetify.breakpoint.name==='xs'
   }          
    }    

}
</script>
