<template>
  <v-row> 
    <v-col col="12" class="col-12">
      <template>
        <v-data-table
          :headers="headers"
          :items="items"
          :search="search"
          :custom-filter="filterOnlyCapsText"
          :loading="loadTable"
          loading-text="Loading... Please wait"
          multi-sort
        >
        
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title v-for="(title, t) in titles" :key="t">
                {{ $vuetify.lang.t('$vuetify.chi') }} {{ $vuetify.lang.t(title['text']) }}
              </v-toolbar-title>
              <v-divider
                class="mx-4"
                inset
                vertical
              ></v-divider>
              <v-spacer></v-spacer>
              <v-dialog
                v-model="dialog"
                max-width="500px"
              >
                <template v-slot:activator="{ on }">
                  <span v-for="(button, b) in buttons" :key="b">
                    <app-btn
                      color="blue"
                      class="white--text"
                      :to="{ path: button['link'], params: button['params'] }"
                      rel="noopener noreferrer"
                    >
                      <v-icon>
                        {{ button['icon'] }}
                      </v-icon>
                      {{ $vuetify.lang.t(button['text']) }}
                    </app-btn>
                  </span>
                </template>   
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                      <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
              </v-dialog>     
            </v-toolbar>
          </template>
          <template v-slot:item.user_type="{ item }">
            {{ $vuetify.lang.current === 'en' ? item.user_type_eng : item.user_type_chi  }}
          </template>
          <template v-slot:item.school="{ item }">
            {{ $vuetify.lang.current === 'en' ? item.school_eng : item.school_chi  }}
          </template>          
          <template v-slot:item.id="{ item }">
            <v-btn 
              class="mr-2"
              color="blue"
              icon 
              small
              :to="{ name: 'AccountEdit', params: { id: item.id, login_id: item.login }}"
            >
              <v-icon color="blue">mdi-pencil</v-icon>
            </v-btn>

            <v-btn 
              color="red"
              icon 
              small
              @click="deleteItem(item)"
            >
              <v-icon color="red">mdi-delete</v-icon>
            </v-btn>
          </template>    
      <template v-slot:body.prepend>
        <tr :class="{'v-data-table__mobile-table-row':isMobile}">
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}"><v-icon color="gray">mdi-magnify</v-icon></td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-text-field
              v-model="que_chi"
              :label="$vuetify.lang.t('$vuetify.que_chi')"
            ></v-text-field>
          </td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-text-field
              v-model="que_eng"
              :label="$vuetify.lang.t('$vuetify.que_eng')"
            ></v-text-field>            
          </td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-autocomplete
              :items="grade"
              :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
              item-value="id"              
              color="blue"
              item-color="blue"
              :label="$vuetify.lang.t('$vuetify.grade')"
              v-model="gradeSearch"
              clearable
              @change="(event) => usertypeS(event)"
            ></v-autocomplete>            
          </td>          
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-autocomplete
              :items="que_type"
              :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
              item-value="id"              
              color="blue"
              item-color="blue"
              :label="$vuetify.lang.t('$vuetify.que_type')"
              v-model="que_typeSearch"
              clearable
              @change="(event) => usertypeS(event)"
            ></v-autocomplete>            
          </td>          
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-autocomplete
              :items="level"
              :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
              item-value="id"              
              color="blue"
              item-color="blue"
              :label="$vuetify.lang.t('$vuetify.level')"
              v-model="levelSearch"
              clearable
              @change="(event) => usertypeS(event)"
            ></v-autocomplete>            
          </td>
          <td :colspan="`${isMobile?6:1}`" :class="{'v-data-table__mobile-row':isMobile}">
            <v-autocomplete
              :items="category"
              :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
              item-value="id"              
              color="blue"
              item-color="blue"
              :label="$vuetify.lang.t('$vuetify.category')"
              v-model="categorySearch"
              clearable
              @change="(event) => usertypeS(event)"
            ></v-autocomplete>            
          </td>
          <td></td>
        </tr>
      </template>                         
        </v-data-table>
      </template>
    </v-col>
  </v-row>
</template>
<script>
import axios from "axios";
export default {
    name: 'QuestionList',

    data: () => ({
      search: '',
      que_chi: '',
      que_eng: '',
      levelSearch: '',
      categorySearch: '',  
      que_typeSearch: '',
      gradeSearch: '',
      que_type: [],
      level: [],
      category: [],
      grade: [],
      user_type_id: "",  
      usertypeSearch: "",
      schoolSearch: "",
      school_id: "",  
      loadTable: true,
      dialog: false,
      dialogDelete: false,
      titles : [
        {
          text: '$vuetify.quesiton_list',
        },        
      ],
      buttons:[
        {
          text: '$vuetify.create_question',
          link: '/Admin/Question/Create',
          icon: 'mdi-plus',
          params: {page:'Quesiton', action:'Create'},
        },        
      ],
      
      items: []
      
    }),
    
    mounted() {
      this.loadItems(); 
      this.loadUserType();
      this.loadSchool();
    },


    methods: {
      deleteItem (item) {
        this.dialogDelete = true
      },  
      closeDelete () {
        this.dialogDelete = false
      },
      deleteItemConfirm () {
        this.closeDelete()
      }, 

      async loadItems() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.items = [];
        let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/question",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){

          let list_number = 1;
          self.items = response.data.map((items)=>{
            return {
                number: list_number++,
                id: items.que_id,
                que_chi: items.que_chi,
                que_eng: items.que_eng,
                que_type: items.que_type,
                grade: items.grade,
                level: items.level,
                cat_id: items.cat_id,
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
        this.loadTable= false;
      },
      filterOnlyCapsText (value, search, item) {
        return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
      },
      async loadUserType() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.user_type_lists = [];
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/user_type",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.user_type_lists = response.data.map((items)=>{
            return {
                id: items.user_type_id,
                name_chi: items.user_type_chi,
                name_eng: items.user_type_eng,                 
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },       
      async loadSchool() {
        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.school_lists = [];
          //let lang = this.$vuetify.lang.current;
          axios.get(
            "http://192.168.12.159:8003/api.php/school",
            { 
              headers: { 'Content-Type': 'application/json' } 
            }
          ).then(function(response){
            self.school_lists = response.data.map((items)=>{
              return {
                  id: items.school_id,
                  name_chi: items.school_chi,
                  name_eng: items.school_eng,
                  school_type: items.school_type,
                  ...items.fields
              }
            });
          }).catch(function(error){
            console.log(error)
          });
      },
      usertypeS(value) {
        this.usertypeSearch = value == null ? '' : value
      },
      schoolS(value) {
        this.schoolSearch = value == null ? '' : value
      }               
    },
    computed: {   
        headers () {
          return [
            {
              sortable: false,
              text: '#',
              value: 'number',
            },
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.que_chi'),
              value: 'que_chi',
              align: 'left',
            
            },        
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.que_eng'),
              value: 'que_eng',
              align: 'left',
                
            },
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.grade'),
              value: 'grade',
              align: 'left',
                
            },             
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.que_type'),
              value: 'que_type',
              align: 'left',
                
            },            
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.level'),
              value: 'level',
              align: 'left',
                
            },
            {
              sortable: true,
              text: this.$vuetify.lang.t('$vuetify.category'),
              value: 'cat_id',
              align: 'left',
               
            },  
            {
              sortable: false,
              text: this.$vuetify.lang.t('$vuetify.action'),
              value: 'id',
            },
          ]},
  isMobile(){
    return this.$vuetify.breakpoint.name==='xs'
   }          
    }    

}
</script>
