
<template>
<v-row v-if="parseInt(level) >=1 && parseInt(level) <=4">
<v-col cols="12">
  <v-expansion-panels accordion popout style="display: block;" class="mt-2">
    <draggable width="100%">
    <v-expansion-panel
      v-for="item in list"
    >
      <v-expansion-panel-header>
        {{ item.title_chi }} ( {{ item.title_eng }} )    <v-chip
        class="ml-2"
      color="blue lighten-1"
      text-color="white"
      style="flex:none;"
    >
      Level : {{ level }}
    </v-chip>
        <div align="right">

          <v-btn class="mx-1" color="blue"
              icon 
              small @click.native.stop="onDoSomeThing"><v-icon color="blue">mdi-pencil</v-icon></v-btn>


          <v-btn class="mx-1" color="red"
              icon 
              small @click.native.stop="onDoSomeThing"><v-icon color="red">mdi-delete</v-icon></v-btn>

        </div>
      </v-expansion-panel-header>
      <v-lazy :options="{ threshold: 0.5 }" transition="fade-transition">
      <v-expansion-panel-content>
        <CategoryItem :level='parseInt(level)+1' :cat_id="item.cat_id" />
      </v-expansion-panel-content>
      </v-lazy>
    </v-expansion-panel>
    </draggable>
  </v-expansion-panels>

                </v-col>
                <v-col cols="12" md="2" class="my-0 py-0 text-center">
                  新增分類
                  </v-col>                
                <v-col cols="12" md="4" class="my-0 py-0">
                  <v-text-field
                    color="blue"
                    label="科目分類(中文)"
                    name="CatChinese"
                    v-model="CatChinese"
                    required
                  />
                  </v-col>
                <v-col cols="12" md="4" class="my-0 py-0">
                  <v-text-field
                    color="blue"
                    label="科目分類(英文)"
                    name="CatEnglish"
                     v-model="CatEnglish"
                     required
                  />
                  </v-col>
                <v-col cols="12" md="2" class="my-0 py-0 text-center">
                  <v-btn class="white--text ma-2"
                    @click="createCat()"
                    color="green"
                  >
                  <v-icon>
                    mdi-check
                  </v-icon>                  
                    {{ $vuetify.lang.t('$vuetify.submit') }}
                  </v-btn>
                  </v-col>                                    
                </v-row>
<v-row v-else>
<template>
  <v-simple-table class="mt-5 mb-2" style="width:100%;">
    <template v-slot:default>
      
      <tbody>
        <tr
          v-for="item in list"
          :key="item.name"
        >
          <td>{{ item.title_chi }} ( {{ item.title_eng }} ) <v-chip
      class="ma-2"
      color="blue"
      text-color="white"
      style="flex:none;"
    >
      Level : {{ level }}
    </v-chip></td>
          <td align="right">          <v-btn class="mx-1" color="blue"
              icon 
              small @click.native.stop="onDoSomeThing"><v-icon color="blue">mdi-pencil</v-icon></v-btn>


          <v-btn class="mx-1" color="red"
              icon 
              small @click.native.stop="onDoSomeThing"><v-icon color="red">mdi-delete</v-icon></v-btn></td>
        </tr>
      </tbody>
      
    </template>
  </v-simple-table>
</template>

</v-row>                
</template>

<style>
.v-expansion-panel-header--active {
  background-color:#FFEB3B;
  color:black;
  
}

.v-expansion-panel-content__wrap {
  background-color:#fff6a6;
}
</style>
<script>
import axios from "axios";
import draggable from "vuedraggable";
import CategoryItem from '@/components/CategoryItem';

export default {
    name: 'CategoryItem',
    props: ['level', 'cat_id'],
    nextlevel: 2,
  order: 10,
  components: {
    draggable, CategoryItem
  },
  data() {
    return {
      CatChinese: '',
      CatEnglish: '',
      list: [],
      activeNames: [1],
      collapseComponentData: {
        on: {
          input: this.inputChanged
        },
        props: {
          value: this.activeNames
        }
      }
    };
  },
    mounted() {
      this.loadCat(); 
    },  
  methods: {
    inputChanged(val) {
      this.activeNames = val;
    },
    onDoSomeThing() {
      console.log('test');
    },
    createCat() {
      console.log(this.CatChinese, this.CatEnglish);
      this.list.push({title: this.CatChinese});
      this.CatChinese = '';
    },
      async loadCat() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/category" + (self.cat_id ? '/'+self.cat_id : ''),
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.list = response.data.map((items)=>{
            return {
                cat_id: items.cat_id,
                title_chi: items.cat_name_chi,
                title_eng: items.cat_name_eng,                 
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },    
  }  
}
</script>