<template>
          <form ref="form" @submit.prevent="createAccount">
            <v-container class="py-0">
              <v-row>
                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    color="blue"
                    :label="$vuetify.lang.t('$vuetify.login_name')"
                    v-model="username"
                    name="username"
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    :append-icon="show_pw ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show_pw ? 'text' : 'password'"
                    color="blue"
                    :label="$vuetify.lang.t('$vuetify.password')"
                    v-model="password"
                    @click:append="show_pw = !show_pw"
                    name="password"
                  />
                </v-col> 
                <v-col
                  cols="12"
                  md="4"
                >
                <v-autocomplete
                  :items="school_type_lists"
                  :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
                  item-value="id"
                  return-object
                  color="blue"
                  item-color="blue"
                  :label="$vuetify.lang.t('$vuetify.school_type')"
                  v-model="school_type_id"
                  @change="(event) => updateSchoolList(event)"
                ></v-autocomplete>
                </v-col>                 
                <v-col
                  cols="12"
                  md="4"
                >                
                <v-autocomplete
                  :items="school_lists"
                  :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
                  item-value="id"
                  return-object                  
                  color="blue"
                  item-color="blue"
                  :label="$vuetify.lang.t('$vuetify.school')"
                  v-model="school_id"
                ></v-autocomplete>
                </v-col>                 
                <v-col
                  cols="12"
                  md="4"
                >
                <v-autocomplete
                  :items="user_type_lists"
                  :item-text= "item =>$vuetify.lang.current === 'en' ? item.name_eng : item.name_chi"
                  item-value="id"
                  return-object                  
                  color="blue"
                  item-color="blue"
                  :label="$vuetify.lang.t('$vuetify.user_type')"
                  v-model="user_type_id"
                ></v-autocomplete>
                </v-col>                                
                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    color="blue"
                    :label="$vuetify.lang.t('$vuetify.name_chi')"
                    v-model="acc_name_chi"
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    color="blue"
                    :label="$vuetify.lang.t('$vuetify.name_eng')"
                    v-model="acc_name_eng"
                  />
                </v-col>
                <v-col
                  cols="12"
                  class="text-center"
                >
                  <v-btn class="white--text ma-2"
                    type="submit"
                    color="green"
                    min-width="150"
                  >
                  <v-icon>
                    mdi-check
                  </v-icon>                  
                    {{ $vuetify.lang.t('$vuetify.submit') }}
                  </v-btn>
                  <v-btn class="white--text ma-2"
                    color="grey"
                    min-width="150"
                    @click="$router.go(-1)"
                  >
                  <v-icon>
                    mdi-undo
                  </v-icon>                       
                    {{ $vuetify.lang.t('$vuetify.back') }}
                  </v-btn>                  
                </v-col>
              </v-row>
            </v-container>
          </form>
</template>

<script>
import axios from "axios";
import VImageInput from 'vuetify-image-input';

const apiClient = new axios.create({
  baseURL: 'https://ncn11iius9.execute-api.us-east-1.amazonaws.com/default',
  withCredentials: false,
  headers: {
    'Accept': 'application/json',
    'Content-Type': 'application/json',
  },
})


export default {
  data: () => {
    return {

        username: "",
        password: "",
        acc_name_chi: "",
        acc_name_eng: "",
        school_type_id: "",
        school_id: "",
        user_type_id: "",
        school_type_lists: [
        /*  {id:0, name:'Kindergarten'},
          {id:1, name:'Primary School'},
          {id:2, name:'Middle School'} */
        ],
        full_school_lists: [],
        school_lists: [
        /*  {id:0, name:'Modern Kindergarten'},
          {id:1, name:'Modern Primary School'},
          {id:2, name:'Modern Middle School'} */
        ],
        user_type_lists: [
        /*  {id:0, name:'Principal'},
          {id:1, name:'Teacher'},
          {id:2, name:'Student'} */
        ],
        show_pw: false, 
    }
     
  },
  name: 'AccountForm',
  components: {
    VImageInput,
  },
  mounted() {
    this.loadSchool();
    this.loadSchoolType(); 
    this.loadUserType();
    this.loadUserInfo();
  },       
  methods: {
      async loadSchool(school_type_id = null, school_select = false) {
        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.full_school_lists = this.full_school_lists.length != 0 ? this.full_school_lists : [];
        this.school_lists = [];
        if(school_type_id == null) {
          
          
          //let lang = this.$vuetify.lang.current;
          axios.get(
            "http://192.168.12.159:8003/api.php/school"+(school_type_id == null ? '' : ('/'+school_type_id)),
            { 
              headers: { 'Content-Type': 'application/json' } 
            }
          ).then(function(response){
            if(!Array.isArray(response.data)) {
              response.data = [response.data];
            }
            self.full_school_lists = response.data.map((items)=>{
              return {
                  id: items.school_id,
                  name_chi: items.school_chi,
                  name_eng: items.school_eng,
                  school_type: items.school_type,
                  ...items.fields
              }
            });
            self.school_lists = self.full_school_lists;
          }).catch(function(error){
            console.log(error)
          });
        } else {
            if(school_select == false) {
              self.school_id = "";
            }
            self.school_lists = self.full_school_lists.filter(f => f.school_type.includes(school_type_id));
        }
      },

      async loadSchoolType() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.school_type_lists = [];
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/school_type",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.school_type_lists = response.data.map((items)=>{
            return {
                id: items.school_type_id,
                name_chi: items.school_type_chi,
                name_eng: items.school_type_eng,                
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },

      async loadUserType() {

        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.user_type_lists = [];
        //let lang = this.$vuetify.lang.current;
        axios.get(
          "http://192.168.12.159:8003/api.php/user_type",
          { 
            headers: { 'Content-Type': 'application/json' } 
          }
        ).then(function(response){
          self.user_type_lists = response.data.map((items)=>{
            return {
                id: items.user_type_id,
                name_chi: items.user_type_chi,
                name_eng: items.user_type_eng,                 
                ...items.fields
            }
          })
        }).catch(function(error){
          console.log(error)
        });
      },  
      
      async loadUserInfo() {
        
        // Init variables
        var self = this
        var app_id = "appID";
        var app_key = "key";
        this.user_info = [];
        var acc_id = this.$route.params.id ? this.$route.params.id : null;
        if(acc_id != null) {
          axios.get(
            "http://192.168.12.159:8003/api.php/user/"+acc_id,
            { 
              headers: { 'Content-Type': 'application/json' } 
            }
          ).then(function(response){
            self.user_info        = response.data;
            self.username       = self.user_info.login_name;
            self.password         = self.user_info.password;
            self.acc_name_chi     = self.user_info.acc_name_chi;
            self.acc_name_eng     = self.user_info.acc_name_eng;
            self.school_type_id   = self.user_info.school_type_id;
            self.school_id        = self.user_info.school_id;
            self.user_type_id     = self.user_info.user_type_id;
            if(self.school_type_id != null) {
              self.loadSchool(self.school_type_id, true);
            }
          }).catch(function(error){
            console.log(error)
          });
        }
      },
      updateSchoolList(value) {
        if(value == null) {
          this.school_lists = this.full_school_lists;
        } else {
          this.loadSchool(value.id, false);
        }
      },
 
 async createAccount(e) {
    try {
      const res = await apiClient.post('/function-create-account', JSON.stringify({
              username: this.username,
              password: this.password,
            }))
      console.log(`Posted ok! ${res.data}`)
      return res
    } catch (err) {
      console.error(err)
      throw err
    }
  },       
        
  },
}
</script>
