// Imports
import Vue from 'vue'
import Router from 'vue-router'
import auth from '@/middleware/auth'

import { trailingSlash } from '@/util/helpers'
import {
  layout,
  route,
} from '@/util/routes'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) return { selector: to.hash }
    if (savedPosition) return savedPosition

    return { x: 0, y: 0 }
  },
  routes: [
    layout('login', [
      route('AdminLogin',null, ''),
      route('Login',null, 'Login'),
      route('AdminLogin',null, 'Admin/Login'),
    ]),

    layout('Default', [
      route('Home',null, 'Admin/Home', { middleware: [auth], breadCrumb: [ { text: '$vuetify.home'} ] }),

      //Account
      route('Account', null, 'Admin/Account', { middleware: [auth], breadCrumb: [ { text: '$vuetify.account'} ] }),
      route('AccountCreate', null, 'Admin/Account/Create', { middleware: [auth], breadCrumb: [ { text: '$vuetify.account', to: { name: 'Account' }}, { text: '$vuetify.create_account'} ] }),
      route('AccountEdit', null, 'Admin/Account/Edit/:id', { middleware: [auth], breadCrumb: [ { text: '$vuetify.account', to: { name: 'Account' }}, { text: '$vuetify.edit_account'}] }),
      route('Maintenance', null, 'Admin/AccountRight/Edit/:id'),
      route('Maintenance', null, 'Admin/School/Edit/:id'),
      route('Maintenance', null, 'Admin/AccountRight/Create'),
      route('Maintenance', null, 'Admin/School/Create'),
      route('AccountCreate2', null, 'Admin/AccountCreate2'),
      //Subject
      route('Subject', null, 'Admin/Subject', { middleware: [auth], breadCrumb: [ { text: '$vuetify.subject'} ] }),
      //Question
      route('Question', null, 'Admin/Question', { middleware: [auth], breadCrumb: [ { text: '$vuetify.question'} ] }),
      route('QuestionCreate', null, 'Admin/Question/Create', { middleware: [auth], breadCrumb: [ { text: '$vuetify.question', to: { name: 'Question' }}, { text: '$vuetify.create_question'} ] }),
                      
      //Assignment
      route('Maintenance', null, 'Admin/Assignment', { middleware: [auth], breadCrumb: [ { text: '$vuetify.assignment'} ] }),    

      //Profile
      route('Maintenance', null, 'Admin/Profile', { middleware: [auth] }), 
      //Setting
      route('Maintenance', null, 'Admin/Settings', { middleware: [auth] }), 
      
      //Dashboard
      route('Dashboard', null, 'Dashboard', { middleware: [auth] }),

      // Pages
      route('UserProfile', null, 'components/profile', { middleware: [auth] }),

      // Components
      route('Notifications', null, 'components/notifications', { middleware: [auth] }),
      route('Icons', null, 'components/icons', { middleware: [auth] }),
      route('Typography', null, 'components/typography', { middleware: [auth] }),

      // Tables
      route('Regular Tables', null, 'tables/regular', { middleware: [auth] }),

      // Maps
      route('Google Maps', null, 'maps/google', { middleware: [auth] }),
    ]),
    
    
  ],
})

/*
router.beforeEach((to, from, next) => {
  return to.path.endsWith('/') ? next() : next(trailingSlash(to.path))
})
*/


// Creates a `nextMiddleware()` function which not only
// runs the default `next()` callback but also triggers
// the subsequent Middleware function.
function nextFactory(context, middleware, index) {
    const subsequentMiddleware = middleware[index];
    // If no subsequent Middleware exists,
    // the default `next()` callback is returned.
    if (!subsequentMiddleware) return context.next;
  
    return (...parameters) => {
      // Run the default Vue Router `next()` callback first.
      context.next(...parameters);
      // Then run the subsequent Middleware with a new
      // `nextMiddleware()` callback.
      const nextMiddleware = nextFactory(context, middleware, index + 1);
      subsequentMiddleware({ ...context, next: nextMiddleware });
    };
  }
  
  router.beforeEach((to, from, next) => {
    if (to.meta.middleware) {
      const middleware = Array.isArray(to.meta.middleware)
        ? to.meta.middleware
        : [to.meta.middleware];
  
      const context = {
        from,
        next,
        router,
        to,
      };

      const nextMiddleware = nextFactory(context, middleware, 1);
  
      return middleware[0]({ ...context, next: nextMiddleware });
    }
  
    return next();
  });
  


export default router
